import React, { Component } from 'react';
import { Icon } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Tabs from 'antd-mobile/lib/tabs';
import WhiteSpace from 'antd-mobile/lib/white-space';

import './commission.css';
import '../../component/apis';

const tabs = [
  { title: '' },
  { title: '' },
];

class CashPage extends Component {
  constructor() {
    super();
    this.state = {
      shows: false,
    }
  }

  componentWillMount() {
    axios.post(global.constants.website+'/api/index/usermoneylog', {
      type: 2,
    },
    {
      headers: {AppAuthorization: localStorage.getItem("token")}
    })
    .then( res => {
      let datas = res.data.data;
      // console.log(res.data.data);
      if ( datas ) {
        this.setState({
          shows: true,
          commissionList: datas,
        })
      }
    })
    .catch(error => {
      console.log(error);
    });
  }

  onChange =(e) => {
    // tabs  
    axios.post(global.constants.website+'/api/index/usermoneylog', {
      type: e.title === "" ? 1 : 2,
    },
    {
      headers: {AppAuthorization: localStorage.getItem("token")}
    })
    .then( res => {
      let datas = res.data.data;
      // console.log(res.data.data);
      if ( datas ) {
        this.setState({
          shows: true,
          commissionList: datas,
        })
      }
    })
    .catch(error => {
      console.log(error);
    });
  }

  render() {
    const { shows, commissionList } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myCenter"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <WhiteSpace style={{ paddingTop: '3rem' }} />
        <Tabs tabs={tabs} initialPage={1} animated={true} useOnPan={false} onTabClick={this.onChange}>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {
              shows ?
                <ul className="commission-list">
                  {
                    commissionList.length ?
                      commissionList.map((item, index) => {
                        return(
                          <li key={item.id}>
                            <div>
                              <span>{item.log_content}</span>
                              <span className="moneys">{item.money}</span>
                            </div>
                            <div className="money-data">
                              <span>{item.dateline}</span>
                              <span>{item.account_money}</span>
                            </div>
                          </li>
                        )
                      })
                    :
                    <div></div>
                  }
                </ul>
              :
              <div className="loading">
                <img src={require("../../img/loading.gif")} alt="loading"/>
                <p>...</p>
              </div>
            }
          </div>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {
              shows ?
                <ul className="commission-list">
                  {
                    commissionList.length ?
                      commissionList.map((item, index) => {
                        return(
                          <li key={item.id}>
                            <div>
                              <span>{item.log_content}</span>
                              <span className="moneys">{item.money}</span>
                            </div>
                            <div className="money-data">
                              <span>{item.dateline}</span>
                              <span>{item.account_money}</span>
                            </div>
                          </li>
                        )
                      })
                    :
                    <div></div>
                  }
                </ul>
              :
              <div className="loading">
                <img src={require("../../img/loading.gif")} alt="loading"/>
                <p>...</p>
              </div>
            }
          </div>
        </Tabs>
        <WhiteSpace />
      </div>
    )
  }
}

export default CashPage
