import React, { Component } from 'react';
import { Icon, Button } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';
import Tabs from 'antd-mobile/lib/tabs';
import WhiteSpace from 'antd-mobile/lib/white-space';

import '../dianFuTask/dianFuTask.css';
import '../../../component/apis';

const tabs = [
  { title: '' },
  { title: '..' },
  { title: '' },
  { title: '' },
];

class WenDaTaskNo extends Component {
  constructor() {
    super();
    this.state = {
      datasShow: false,         //true 
    }
  }

  componentWillMount() {
    axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
      status: 5
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res => {
      let resData = res.data.data;
      // console.log(resData);
      this.setState({
        datasShow: true,                              //true 
        task_lists: resData.task_list,                //
      });
    })
    .catch( error => {
      console.log(error);
    })
  }

  // Tabs 
  onChange =(e) => {
    // console.log(e.title);
    if ( e.title === "" ) {
      axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
        status: 0,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res.data.data.task_list);
        this.setState({
          statea: res.data.data.task_list,
        })
      })
      .catch( error => {
        console.log(error);
      })
    } else if ( e.title === ".." ) {
      axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
        status: 1,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res.data.data);
        this.setState({
          stateb: res.data.data.task_list,
        })
      })
      .catch( error => {
        console.log(error);
      })
    } else if ( e.title === "" ) {
      axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
        status: 4,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        this.setState({
          statec: res.data.data.task_list,
        })
      })
      .catch( error => {
        console.log(error);
      })
    } else {
      axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
        status: 5,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res.data.data);
      })
      .catch( error => {
        console.log(error);
      })
    }
  }

  render() {
    const { task_lists,datasShow,statea,stateb,statec } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myTask"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <WhiteSpace style={{ paddingTop: '3rem' }} />
        <Tabs tabs={tabs} initialPage={3} animated={false} useOnPan={false} onTabClick={this.onChange}>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {/*  all-task div */}
            {
              statea ?
                statea.map((item, index) => {
                  return(
                    <div key={index} className="all-task">
                      <div className="left-img">
                        <img src={item.goodspic} alt=""/>
                      </div>
                      <div className="right">
                        {/* top */}
                        <div className="right-top">
                          <span className="right-top-child">
                            <img className="right-top-childImg" src={item.taskitem_pic} alt=""/>
                            {item.user_taobao}
                          </span>
                          <span>
                            {item.addtime}
                          </span>
                        </div>
                        {/* center */}
                        <div className="right-center">
                          <p>{item.commission}</p>
                        </div>
                      </div>
                    </div>
                  )
                })
              :
              <div className="taskLists">,^-^</div>
            }
          </div>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {
              stateb ?
                stateb.map((item, index) => {
                  return(
                    <div key={index} className="all-task">
                      <div className="left-img">
                        <img src={item.goodspic} alt=""/>
                      </div>
                      <div className="right">
                        {/* top */}
                        <div className="right-top">
                          <span className="right-top-child">
                            <img className="right-top-childImg" src={item.taskitem_pic} alt=""/>
                            {item.user_taobao}
                          </span>
                          <span>
                            {item.addtime}
                          </span>
                        </div>
                        {/* center */}
                        <div className="right-center">
                          <p>{item.commission}</p>
                          <Button type="primary"><Link to={{ pathname: '/questionsTasks', state: item.order_id }}>{item.order_status_texts}</Link></Button>
                        </div>
                      </div>
                    </div>
                  )
                })
              :
              <div className="taskLists">,^-^</div>
            }
          </div>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {
              statec ?
                statec.map((item, index) => {
                  return(
                    <div key={index} className="all-task">
                      <div className="left-img">
                        <img src={item.goodspic} alt=""/>
                      </div>
                      <div className="right">
                        {/* top */}
                        <div className="right-top">
                          <span className="right-top-child">
                            <img className="right-top-childImg" src={item.taskitem_pic} alt=""/>
                            {item.user_taobao}
                          </span>
                          <span>
                            {item.addtime}
                          </span>
                        </div>
                        {/* center */}
                        <div>
                          <div className="right-center">
                            <p>{item.commission}</p>
                            {
                              item.order_status === 1 ?
                                <Button type="primary"><Link to={{ pathname: '/questionsTasks', state: item.order_id }}>{item.order_status_texts}</Link></Button>
                              :
                              ""
                            }
                          </div>
                        </div>
                      </div>
                    </div>
                  )
                })
              :
              <div className="taskLists">,^-^</div>
            }
          </div>
          {/*  */}
          <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff' }}>
            {/*  all-task div */}
            {
              datasShow ?
                task_lists.length ?
                  task_lists.map((item, index) => {
                    return(
                      <div className="all-task" key={index}>
                        <div className="left-img">
                          <img src={ item.goodspic } alt=""/>
                        </div>
                        <div className="right">
                          {/* top */}
                          <div className="right-top">
                            <span className="right-top-child">
                              <img className="right-top-childImg" src={item.taskitem_pic} alt=""/>
                              {item.user_taobao}
                            </span>
                            <span>
                              {item.addtime}
                            </span>
                          </div>
                          {/* center */}
                          {/*  */}
                          <div className="right-center">
                            <p>{item.commission}</p>
                            {
                              item.order_status === 1 ?
                                <Button type="primary"><Link to={{ pathname: '/questionsTasks', state: item.order_id }}>{item.order_status_texts}</Link></Button>
                              :
                              ""
                            }
                          </div>
                        </div>
                      </div>
                    )
                  })
                :
                <div className="taskLists">,^-^</div>
              :
              <div className="loading">
                <img src={require("../../../img/loading.gif")} alt="loading"/>
                <p>...</p>
              </div>
            }
          </div>
        </Tabs>
        <WhiteSpace />
      </div>
    )
  }
}

export default WenDaTaskNo
