import React, { Component } from 'react';
import { Icon, Button } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';
import '../../../component/apis';

class WenDaTaskOk extends Component {
  constructor() {
    super()
    this.state ={
      datasState: false,      //ajax 
    }
  }

  componentWillMount() {
    axios.post(global.constants.website+'/api/task/mytaskanswerlist',{
      status: 9,             //
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res => {
      // console.log(res.data.data.task_list);
      this.setState({
        task_lists: res.data.data.task_list,
        datasState: true,                       //true
      })
    })
    .catch( err => {
      console.log(err);
    })
  }

  render() {
    const { task_lists,datasState } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myTask"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div style={{ padding: '0.3rem 0.3rem', backgroundColor: '#fff', paddingTop: '3rem' }}>
          {/*  all-task div */}
          {
            datasState ?
              task_lists ?
                task_lists.map((item, index) => {
                  return(
                    <div key={index} className="all-task">
                      <div className="left-img">
                        <img src={item.goodspic} alt=""/>
                      </div>
                      <div className="right">
                        {/* top */}
                        <div className="right-top">
                          <span className="right-top-child">
                            <img className="right-top-childImg" src={item.taskitem_pic} alt=""/>
                            {item.user_taobao}
                          </span>
                          <span>
                            {item.addtime}
                          </span>
                        </div>
                        {/* center */}
                        <div>
                          <div className="right-center">
                            <p>{item.commission}</p>
                            <Button type="primary">{item.order_status_texts}</Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  )
                })
              :
              <div className="taskLists">,^-^</div>
            :
            <div className="loading">
              <img src={require("../../../img/loading.gif")} alt="loading"/>
              <p>...</p>
            </div>
            // Toast.loading('...')
          }
        </div>
      </div>
    )
  }
}

export default WenDaTaskOk
